/**
 * Angular wrapper for Bryntum ResourceFilter
 */

import { Component, ElementRef, EventEmitter, OnDestroy, OnInit, Output, Input, SimpleChange, SimpleChanges } from '@angular/core';
import WrapperHelper from './wrapper.helper';

// Bryntum 'scheduler.lite.umd' bundle comes without polyfills to support Angular's zone.js
import { Base, Collection, CollectionConfig, EventStore, Mask, MaskConfig, Rectangle, Scroller, ScrollerConfig, Store, StoreConfig, Widget } from '@bryntum/scheduler/scheduler.lite.umd.js';
import { ResourceFilter, StringHelper, WidgetHelper } from '@bryntum/scheduler/scheduler.lite.umd.js';

@Component({
    selector : 'bryntum-resource-filter',
    template : ''
})
export class BryntumResourceFilterComponent implements OnInit, OnDestroy {

    public static instanceClass = ResourceFilter;

    private static bryntumFeatureNames: string[] = [

    ];

    private static bryntumConfigs: string[] = BryntumResourceFilterComponent.bryntumFeatureNames.concat([
        'activateOnMouseover',
        'adopt',
        'align',
        'alignSelf',
        'allowGroupSelect',
        'anchor',
        'appendTo',
        'bubbleEvents',
        'centered',
        'cls',
        'config',
        'constrainTo',
        'content',
        'contentElementCls',
        'dataset',
        'defaultBindProperty',
        'disabled',
        'dock',
        'draggable',
        'eventStore',
        'extraData',
        'flex',
        'floating',
        'groupHeaderTpl',
        'height',
        'hidden',
        'hideAnimation',
        'html',
        'htmlCls',
        'id',
        'insertBefore',
        'insertFirst',
        'items',
        'itemTpl',
        'listeners',
        'localeClass',
        'localizableProperties',
        'margin',
        'maskDefaults',
        'masked',
        'maxHeight',
        'maxWidth',
        'minHeight',
        'minWidth',
        'monitorResize',
        'multiSelect',
        'owner',
        'positioned',
        'preventTooltipOnTouch',
        'readOnly',
        'ripple',
        'rootElement',
        'scrollable',
        'scrollAction',
        'selectAllItem',
        'selected',
        'showAnimation',
        'store',
        'tab',
        'tag',
        'textAlign',
        'title',
        'toggleAllIfCtrlPressed',
        'tooltip',
        'weight',
        'width',
        'x',
        'y'
    ]);

    private static bryntumConfigsOnly: string[] = [
        'activateOnMouseover',
        'adopt',
        'align',
        'allowGroupSelect',
        'anchor',
        'appendTo',
        'bubbleEvents',
        'centered',
        'cls',
        'config',
        'constrainTo',
        'contentElementCls',
        'defaultBindProperty',
        'dock',
        'draggable',
        'eventStore',
        'floating',
        'groupHeaderTpl',
        'hideAnimation',
        'htmlCls',
        'insertBefore',
        'insertFirst',
        'itemTpl',
        'listeners',
        'localeClass',
        'localizableProperties',
        'maskDefaults',
        'masked',
        'monitorResize',
        'multiSelect',
        'owner',
        'positioned',
        'preventTooltipOnTouch',
        'ripple',
        'rootElement',
        'scrollAction',
        'selectAllItem',
        'selected',
        'showAnimation',
        'store',
        'tab',
        'tag',
        'textAlign',
        'title',
        'toggleAllIfCtrlPressed',
        'weight'
    ];

    private static bryntumProps: string[] = BryntumResourceFilterComponent.bryntumFeatureNames.concat([
        'alignSelf',
        'anchorSize',
        'content',
        'dataset',
        'disabled',
        'extraData',
        'flex',
        'height',
        'hidden',
        'html',
        'id',
        'items',
        'margin',
        'maxHeight',
        'maxWidth',
        'minHeight',
        'minWidth',
        'readOnly',
        'scrollable',
        'tooltip',
        'width',
        'x',
        'y'
    ]);

    private elementRef: ElementRef;
    public instance: ResourceFilter;

    private bryntumConfig = {
        angularComponent : this,
        adopt            : undefined,
        listeners        : {},
        features         : {}
    };

    constructor(element: ElementRef) {
        this.elementRef = element;
    }

     // Configs only
    @Input() activateOnMouseover    : boolean;
    @Input() adopt                  : HTMLElement|string;
    @Input() align                  : object|string;
    @Input() allowGroupSelect       : boolean;
    @Input() anchor                 : boolean;
    @Input() appendTo               : HTMLElement|string;
    @Input() bubbleEvents           : object;
    @Input() centered               : boolean;
    @Input() cls                    : string|object;
    @Input() config                 : object;
    @Input() constrainTo            : HTMLElement|Widget|Rectangle;
    @Input() contentElementCls      : string|object;
    @Input() defaultBindProperty    : string;
    @Input() dock                   : string;
    @Input() draggable              : boolean|object;
    @Input() eventStore             : EventStore;
    @Input() floating               : boolean;
    @Input() groupHeaderTpl         : Function;
    @Input() hideAnimation          : boolean|object;
    @Input() htmlCls                : string|object;
    @Input() insertBefore           : HTMLElement|string;
    @Input() insertFirst            : HTMLElement|string;
    @Input() itemTpl                : Function;
    @Input() listeners              : object;
    @Input() localeClass            : Base;
    @Input() localizableProperties  : string[];
    @Input() maskDefaults           : object|Mask|Partial<MaskConfig>;
    @Input() masked                 : boolean|string|object|Mask|Partial<MaskConfig>;
    @Input() monitorResize          : boolean;
    @Input() multiSelect            : boolean;
    @Input() owner                  : Widget;
    @Input() positioned             : boolean;
    @Input() preventTooltipOnTouch  : boolean;
    @Input() ripple                 : boolean|object;
    @Input() rootElement            : ShadowRoot;
    @Input() scrollAction           : string;
    @Input() selectAllItem          : boolean|string;
    @Input() selected               : Collection|object|Partial<CollectionConfig>;
    @Input() showAnimation          : boolean|object;
    @Input() store                  : object|Store|Partial<StoreConfig>;
    @Input() tab                    : boolean|object;
    @Input() tag                    : string;
    @Input() textAlign              : string;
    @Input() title                  : string;
    @Input() toggleAllIfCtrlPressed : boolean;
    @Input() weight                 : number;

     // Configs and properties
    @Input() alignSelf  : string;
    @Input() content    : string;
    @Input() dataset    : object;
    @Input() disabled   : boolean;
    @Input() extraData  : boolean|number|object|string;
    @Input() flex       : number|string;
    @Input() height     : number|string;
    @Input() hidden     : boolean;
    @Input() html       : string;
    @Input() id         : string;
    @Input() items      : object[];
    @Input() margin     : number|string;
    @Input() maxHeight  : number|string;
    @Input() maxWidth   : number|string;
    @Input() minHeight  : number|string;
    @Input() minWidth   : number|string;
    @Input() readOnly   : boolean;
    @Input() scrollable : boolean|Scroller|object|Partial<ScrollerConfig>;
    @Input() tooltip    : object|string;
    @Input() width      : number|string;
    @Input() x          : number;
    @Input() y          : number;

     // Properties only
    @Input() anchorSize : number[];

     // Events
    @Output() onBeforeDestroy = new EventEmitter<object>();
    @Output() onBeforeHide    = new EventEmitter<object>();
    @Output() onBeforeShow    = new EventEmitter<object>();
    @Output() onCatchAll      = new EventEmitter<object>();
    @Output() onDestroy       = new EventEmitter<object>();
    @Output() onFocusIn       = new EventEmitter<object>();
    @Output() onFocusOut      = new EventEmitter<object>();
    @Output() onHide          = new EventEmitter<object>();
    @Output() onItem          = new EventEmitter<object>();
    @Output() onPaint         = new EventEmitter<object>();
    @Output() onReadOnly      = new EventEmitter<object>();
    @Output() onResize        = new EventEmitter<object>();
    @Output() onShow          = new EventEmitter<object>();

    /**
     * Create and append the underlying widget
     */
    ngOnInit(): void {
        const
            me = this,
            {
                elementRef,
                bryntumConfig
            } = me,
            {
                instanceClass,
                bryntumConfigs
            } = BryntumResourceFilterComponent;

        Object.assign(bryntumConfig, {
            listeners : {
                beforeDestroy(event : any) { me.onBeforeDestroy.emit(event); },
                beforeHide(event : any) { me.onBeforeHide.emit(event); },
                beforeShow(event : any) { me.onBeforeShow.emit(event); },
                catchAll(event : any) { me.onCatchAll.emit(event); },
                destroy(event : any) { me.onDestroy.emit(event); },
                focusIn(event : any) { me.onFocusIn.emit(event); },
                focusOut(event : any) { me.onFocusOut.emit(event); },
                hide(event : any) { me.onHide.emit(event); },
                item(event : any) { me.onItem.emit(event); },
                paint(event : any) { me.onPaint.emit(event); },
                readOnly(event : any) { me.onReadOnly.emit(event); },
                resize(event : any) { me.onResize.emit(event); },
                show(event : any) { me.onShow.emit(event); },

                thisObj : me
            }
        });

        // If component has no container specified in config then use adopt to Wrapper's element
        const
            containerParam = [
                'adopt',
                'appendTo',
                'insertAfter',
                'insertBefore'
            ].find(prop => bryntumConfig[prop]);
        if (!containerParam) {
            bryntumConfig.adopt = elementRef.nativeElement;
        }
        else {
            WrapperHelper.devWarningContainer(instanceClass.$name, containerParam);
        }

        bryntumConfigs.filter(prop => prop in this).forEach(prop => {
            WrapperHelper.applyPropValue(bryntumConfig, prop, this[prop]);
            if (['features', 'config'].includes(prop)) {
                WrapperHelper.devWarningConfigProp(instanceClass.$name, prop);
            }
        });

        // @ts-ignore
        me.instance = instanceClass.$name === 'Widget' ? WidgetHelper.createWidget(bryntumConfig) : new instanceClass(bryntumConfig);

    }

    /**
     * Watch for changes
     * @param changes
     */
    ngOnChanges(changes: SimpleChanges): void {
        const
            { instance } = this,
            { instanceClass } = BryntumResourceFilterComponent;

        if (!instance) {
            return;
        }

        // Iterate over all changes
        Object.entries(changes).forEach(([prop, change]) => {
            const
                newValue = (change as SimpleChange).currentValue,
                { instance } = this,
                { bryntumConfigsOnly, bryntumProps } = BryntumResourceFilterComponent;
            if (bryntumProps.includes(prop)) {
                WrapperHelper.applyPropValue(instance, prop, newValue, false);
                if (bryntumConfigsOnly.includes(prop)) {
                    WrapperHelper.devWarningUpdateProp(instanceClass.$name, prop);
                }
            }
        });
    }

    /**
     * Destroy the component
     */
    ngOnDestroy(): void {
        if (this.instance) {
            this.instance.destroy();
        }
    }
}
