    /**
     * Create and append the underlying widget
     */
    ngOnInit(): void {
        const
            me = this,
            {
                elementRef,
                bryntumConfig
            } = me,
            {
                instanceClass,
                bryntumConfigs
            } = BryntumGridComponent;

        Object.assign(bryntumConfig, {
            listeners : {
                beforeCellEditStart(event : any) { me.onBeforeCellEditStart.emit(event); },
                beforeDestroy(event : any) { me.onBeforeDestroy.emit(event); },
                beforeExport(event : any) { me.onBeforeExport.emit(event); },
                beforeFinishCellEdit(event : any) { me.onBeforeFinishCellEdit.emit(event); },
                beforeHide(event : any) { me.onBeforeHide.emit(event); },
                beforeRenderRows(event : any) { me.onBeforeRenderRows.emit(event); },
                beforeSetRecord(event : any) { me.onBeforeSetRecord.emit(event); },
                beforeShow(event : any) { me.onBeforeShow.emit(event); },
                beforeToggleNode(event : any) { me.onBeforeToggleNode.emit(event); },
                cancelCellEdit(event : any) { me.onCancelCellEdit.emit(event); },
                catchAll(event : any) { me.onCatchAll.emit(event); },
                cellClick(event : any) { me.onCellClick.emit(event); },
                cellContextMenu(event : any) { me.onCellContextMenu.emit(event); },
                cellDblClick(event : any) { me.onCellDblClick.emit(event); },
                cellMouseOut(event : any) { me.onCellMouseOut.emit(event); },
                cellMouseOver(event : any) { me.onCellMouseOver.emit(event); },
                collapseNode(event : any) { me.onCollapseNode.emit(event); },
                destroy(event : any) { me.onDestroy.emit(event); },
                expandNode(event : any) { me.onExpandNode.emit(event); },
                export(event : any) { me.onExport.emit(event); },
                finishCellEdit(event : any) { me.onFinishCellEdit.emit(event); },
                focusIn(event : any) { me.onFocusIn.emit(event); },
                focusOut(event : any) { me.onFocusOut.emit(event); },
                hide(event : any) { me.onHide.emit(event); },
                mouseOut(event : any) { me.onMouseOut.emit(event); },
                mouseOver(event : any) { me.onMouseOver.emit(event); },
                paint(event : any) { me.onPaint.emit(event); },
                readOnly(event : any) { me.onReadOnly.emit(event); },
                renderRows(event : any) { me.onRenderRows.emit(event); },
                resize(event : any) { me.onResize.emit(event); },
                responsive(event : any) { me.onResponsive.emit(event); },
                scroll(event : any) { me.onScroll.emit(event); },
                selectionChange(event : any) { me.onSelectionChange.emit(event); },
                show(event : any) { me.onShow.emit(event); },
                startCellEdit(event : any) { me.onStartCellEdit.emit(event); },
                toggleNode(event : any) { me.onToggleNode.emit(event); },
                toolClick(event : any) { me.onToolClick.emit(event); },

                thisObj : me
            }
        });

        // If component has no container specified in config then use adopt to Wrapper's element
        const
            containerParam = [
                'adopt',
                'appendTo',
                'insertAfter',
                'insertBefore'
            ].find(prop => bryntumConfig[prop]);
        if (!containerParam) {
            bryntumConfig.adopt = elementRef.nativeElement;
        }
        else {
            WrapperHelper.devWarningContainer(instanceClass.$name, containerParam);
        }

        bryntumConfigs.filter(prop => prop in this).forEach(prop => {
            WrapperHelper.applyPropValue(bryntumConfig, prop, this[prop]);
            if (['features', 'config'].includes(prop)) {
                WrapperHelper.devWarningConfigProp(instanceClass.$name, prop);
            }
        });

        // @ts-ignore
        me.instance = instanceClass.$name === 'Widget' ? WidgetHelper.createWidget(bryntumConfig) : new instanceClass(bryntumConfig);

        // Backwards compatibility for gridInstance, schedulerInstance etc.
        me[StringHelper.uncapitalize(instanceClass.$name) + 'Instance'] = me.instance;
        // 
    }

    /**
     * Watch for changes
     * @param changes
     */
    ngOnChanges(changes: SimpleChanges): void {
        const
            { instance } = this,
            { instanceClass } = BryntumGridComponent;

        if (!instance) {
            return;
        }

        // Iterate over all changes
        Object.entries(changes).forEach(([prop, change]) => {
            const
                newValue = (change as SimpleChange).currentValue,
                { instance } = this,
                { bryntumConfigsOnly, bryntumProps } = BryntumGridComponent;
            if (bryntumProps.includes(prop)) {
                WrapperHelper.applyPropValue(instance, prop, newValue, false);
                if (bryntumConfigsOnly.includes(prop)) {
                    WrapperHelper.devWarningUpdateProp(instanceClass.$name, prop);
                }
            }
        });
    }

    /**
     * Destroy the component
     */
    ngOnDestroy(): void {
        if (this.instance) {
            this.instance.destroy();
        }
    }
