/**
 * Angular wrapper for Bryntum Fullscreen button
 */
import { Component, ElementRef, OnDestroy, OnInit } from '@angular/core';
// Bryntum 'scheduler.lite.umd' bundle comes without polyfills to support Angular's zone.js
import { Button, Fullscreen, WidgetHelper } from '@bryntum/scheduler/scheduler.lite.umd.js';

@Component({
    selector : 'bryntum-fullscreen-button',
    template : ''
})
export class BryntumFullscreenButtonComponent implements OnInit, OnDestroy {
    // class variables
    private elementRef: ElementRef;
    private button: Button;

    constructor(element: ElementRef) {
        this.elementRef = element;
    }

    /**
     * Initializes component
     */
    ngOnInit(): void {
        if (!Fullscreen.enabled) {
            return;
        }
        this.button = WidgetHelper.createWidget({
            type       : 'button',
            appendTo   : this.elementRef.nativeElement,
            icon       : 'b-icon b-icon-fullscreen',
            tooltip    : 'Fullscreen',
            toggleable : true,
            onToggle   : ({ pressed }) => {
                if (pressed) {
                    Fullscreen.request(document.documentElement);
                }
                else {
                    Fullscreen.exit();
                }
            }
        }) as Button;

        Fullscreen.onFullscreenChange(() => {
            this.button['pressed'] = Fullscreen.isFullscreen;
        });
    }

    /**
     * Destroys component
     */
    ngOnDestroy() : void {
        if (this.button) {
            this.button.destroy();
        }
    }
}
